import React from "react";
import "./App.css";
import LineChart from "./components/LineChart";
import ReactSpectrum2Provider from "@react/react-spectrum/Provider";
import {
  Item,
  TabList,
  TabPanels,
  Tabs,
  Provider as ReactSpectrum3Provider,
  defaultTheme,
} from "@adobe/react-spectrum";

function App() {
  return (
    <ReactSpectrum3Provider theme={defaultTheme} colorScheme="light">
      <ReactSpectrum2Provider theme="light">
        <div className="App">
          <Tabs aria-label="Testing Tabs">
            <TabList>
              <Item key="E1">Low Verbosity Graph</Item>
              <Item key="E2">High Verbosity Graph</Item>
            </TabList>
            <TabPanels>
              <Item key="E1">
                <LineChart
                  colors={["#5151d3", "#58e06f", "#d83790", "#e8871a"]}
                  toolTipAccessors={["month", "value"]}
                  toolTipTitle={["Date", "U.S. Dollars in millions"]}
                  locale="en-US"
                />
              </Item>
              <Item key="E2">
                <LineChart
                  colors={["#5151d3", "#58e06f", "#d83790", "#e8871a"]}
                  toolTipAccessors={[
                    "month",
                    "value",
                    "differenceFromLast",
                    "differenceFromNext",
                    "minMax",
                  ]}
                  toolTipTitle={[
                    "Date",
                    "U.S. Dollars in millions",
                    "Distance from last point",
                    "Distance to next point",
                    "Min/Max",
                  ]}
                  locale="en-US"
                  viewTakeaways={true}
                />
              </Item>
            </TabPanels>
          </Tabs>
        </div>
      </ReactSpectrum2Provider>
    </ReactSpectrum3Provider>
  );
}

export default App;
